$branded: #332208;
$white: #fefefe;
$dark: #1b1818;
$accent: #c5ac2d;

html {
    box-sizing: border-box;
}

*,
*::after,
*::before {
    box-sizing: inherit;
}

body {
    margin: 0;
}

ul,
li {
    list-style: none;
}

.container {
    max-width: 1400px;
    margin: 0 auto;
}

.header__inner {
    height: 100px;
    background-color: $branded;
    display: flex;
    justify-content: space-around;
    align-items: center;

    li {
        display: inline-block;
    }

    a {
        text-decoration: none;
        text-transform: uppercase;
        font-family: 'Roboto Condensed', sans-serif;
        font-weight: 400;
        font-size: 18px;
        color: $white;
        padding: 10px;

        &:hover {
            text-decoration: underline;
        }
    }
}

.section__first {
    .section__inner {
        height: 800px;
        display: flex;

        &-block {
            max-width: 600px;
            display: flex;
            flex-direction: column;
            justify-content: space-around;

            h1 {
                margin: 141px auto 0;
                font-family: 'Fira Sans', sans-serif;
                font-weight: 500;
                font-size: 48px;
                color: $dark;
                text-align: center;
            }

            h2 {
                margin: 30px auto 0;
                font-family: 'Roboto Condensed', sans-serif;
                font-weight: 400;
                font-size: 24px;
                color: $dark;
                text-align: center;
                max-width: 460px;
            }

            button.download {
                width: 200px;
                height: 50px;
                border: none;
                margin: 60px auto 0px;
                font-family: 'Roboto Condensed', sans-serif;
                font-weight: 400;
                font-size: 16px;
                color: $white;
                background-color: $accent;
                text-transform: uppercase;
                transition: .7s;

                &:hover {
                    color: $accent;
                    background-color: $white;
                    border: 1px solid $accent;
                }
            }

            .block-items {
                margin-top: 100px;
                display: flex;
                justify-content: space-around;
                align-items: center;

                .block-item {
                    width: 150px;
                    height: 150px;
                    border: 1px solid $dark;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-around;
                    align-items: center;
                    transition: .7s;
                    box-shadow: 0px 4px 2px rgba(0, 0, 0, 0.25);

                    &:hover {
                        transform: scale(1.25);
                    }

                    p {
                        margin: 10px;
                        font-family: 'Roboto Condensed', sans-serif;
                        font-weight: 400;
                        font-size: 16px;
                        color: $dark;
                        text-align: center;
                    }
                }

            }

            .slider.slick-initialized.slick-slider.slick-dotted {
                position: relative;

                ul.slick-dots {
                    position: absolute;
                    top: 120px;
                    left: 592px;
                    display: flex;
                    flex-direction: column;

                    li button {
                        height: 8px;
                        width: 8px;
                        border-radius: 50%;
                        background-color: $branded;
                        transition: .5s;
                        &::before {
                            display: none;
                        }
                    }

                    li.slick-active button {
                        scale: 2;
                    }
                }

                .slider__text {
                    font-family: 'Fira Sans', sans-serif;
                    font-weight: 500;
                    font-size: 16px;
                    color: $dark;
                    text-align: center;
                }
            }

            .references {
                margin-top: 160px;
                display: flex;
                flex-direction: row;
                a {
                    display: block;
                    font-family: 'Roboto Condensed', sans-serif;
                    font-weight: 400;
                    font-size: 16px;
                    color: $dark;
                    text-decoration: none; 
                    &:nth-child(1) {
                        padding: 0px 30px 0px 100px;
                    }
                    &:nth-child(2) {
                        padding: 0px;
                    }
                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
    }
}